import Grid from './grid.js';

class GameView {
  constructor(canvas) {
    this.canvas = canvas;
    this.grid = new Grid(canvas);

    this.init = this.init.bind(this);
  }

  init() {
    this.grid.addBlock([100, 100]);  // this should create event handlers for grid and buttons, etc.
  }
}

export default GameView;



